import React, { useEffect, useState, useContext, useRef } from "react";

import { img_daily, img_review, img_retro } from "../Assets";
import Dialog from "../Components/Dialog";
import Role, { ROLE_NAMES } from "../Components/Role";
import List, { LIST_ITEM_TYPE, LIST_THEME } from "../Components/List";
import Button, { BUTTON_TYPES } from "../Components/Button";
import { Context, STEPS_KEY } from "../Scrum2";

import classes from "../styles.module.scss";
import classNames from "classnames/bind";
const cx = classNames.bind(classes);

const OPENING = "opening";
const INTRO = "intro";
const PROCESS = "process";
const END = "end";
const PARAGRAPH = {
  [OPENING]: (
    <p>
         Sprint   
      <br />
         
    </p>
  ),
  [INTRO]: (
    <p>
      Sprint      
      <span> (Daily Scrum)</span> 
         Sprint 
      <span> (Sprint Review)</span> 
      <span> (Sprint Retrospective)</span> 
    </p>
  ),
  [PROCESS]: (
    <p>
         Surum  
       <br />
      ()
    </p>
  ),
  [END]: <p>   </p>,
};
const DAILY = "daily";
const REVIEW = "review";
const RETRO = "retro";
const INTRO_STEPS = {
  [DAILY]: {
    order: 1,
    imgSrc: img_daily,
    title: "",
    subtitle: "Daily Scrum",
    className: "daily",
    content: (
      <ul>
          15
        <li> (Sprint Goal)</li>
        <li> </li>
        <li></li>
          
      </ul>
    ),
  },
  [REVIEW]: {
    order: 2,
    imgSrc: img_review,
    title: "",
    subtitle: "Sprint Review",
    className: "review",
    content: (
      <ul>   </ul>
    ),
  },
  [RETRO]: {
    order: 3,
    imgSrc: img_retro,
    title: "",
    subtitle: "Sprint Retrospective",
    className: "retrospective",
    content: (
      <ul>
         , 
        <li></li>
        <li></li>
        <li></li>
        
      </ul>
    ),
  },
};
const LIST_ITEMS = [
  {
    title: "",
    subtitle: "Product Backlog",
  },
  {
    title: "",
    subtitle: "Sprint Planing",
  },
  {
    title: "",
    subtitle: "Sprint Backlog",
  },
];

const SprintIntro = () => {
  const upperRef = useRef(null);
  const context = useContext(Context);
  const [nowScene, setNowScene] = useState(OPENING);
  const [introStep, setIntroStep] = useState(0);
  const [processStep, setProcessStep] = useState(0);

  const handleChangeScene = () => {
    switch (nowScene) {
      case OPENING:
        setNowScene(INTRO);
        break;
      case INTRO:
        if (introStep >= Object.keys(INTRO_STEPS).length) {
          setNowScene(PROCESS);
        } else {
          if (introStep === 0) {
            window.scrollTo({ top: upperRef.current.offsetHeight });
          }
          setIntroStep(introStep + 1);
        }
        break;
      case PROCESS:
        if (processStep >= 2) {
          setNowScene(END);
          return;
        }
        window.scrollTo({ top: upperRef.current.offsetHeight });
        setProcessStep(processStep + 1);
        break;
      case END:
        context.goStep(STEPS_KEY.RETRO);
        break;
      default:
        break;
    }
  };

  const renderContent = () => {
    switch (nowScene) {
      case INTRO:
        return (
          <div className={cx("intro")}>
            {Object.keys(INTRO_STEPS).map((key) => (
              <div
                key={key}
                {...(introStep >= INTRO_STEPS[key].order && {
                  className: cx("show"),
                })}
              >
                <img
                  src={INTRO_STEPS[key].imgSrc}
                  alt={INTRO_STEPS[key].subtitle}
                />
                <div className={cx("card", "yellow")}>
                  <p>{INTRO_STEPS[key].title}</p>
                  <span>{INTRO_STEPS[key].subtitle}</span>
                </div>
                {INTRO_STEPS[key].content}
              </div>
            ))}
          </div>
        );
      case PROCESS:
        return (
          <div className={cx("process")}>
            <div className={cx("listContainer")}>
              {LIST_ITEMS.map((item) => (
                <div
                  className={cx(
                    "card",
                    "green",
                    "left",
                    "hide",
                    processStep >= 1 && "jumping"
                  )}
                >
                  <p>{item.title}</p>
                  <span>{item.subtitle}</span>
                </div>
              ))}
            </div>
            <div
              className={cx(
                "card",
                "green",
                "up",
                "hide",
                processStep >= 1 && "jumping"
              )}
            >
              <p></p>
              <span>sprint</span>
            </div>
            <div className={cx("position_ab", "emptyCard", "review")}>
              <List.Item
                type={LIST_ITEM_TYPE.DASHED}
                theme={LIST_THEME.YELLOW}
              ></List.Item>
            </div>
            <div className={cx("position_ab", "emptyCard", "daily")}>
              <List.Item
                type={LIST_ITEM_TYPE.DASHED}
                theme={LIST_THEME.YELLOW}
              ></List.Item>
            </div>
            <div className={cx("position_ab", "emptyCard", "retrospective")}>
              <List.Item
                type={LIST_ITEM_TYPE.DASHED}
                theme={LIST_THEME.YELLOW}
              ></List.Item>
            </div>
            <div className={cx("listContainer")}>
              {Object.keys(INTRO_STEPS).map((key) => (
                <div
                  className={cx(
                    "card",
                    "yellow",
                    INTRO_STEPS[key].className,
                    processStep >= 2 && "position_ab"
                  )}
                >
                  <p>{INTRO_STEPS[key].title}</p>
                  <span>{INTRO_STEPS[key].subtitle}</span>
                </div>
              ))}
            </div>
          </div>
        );
      case END:
        return (
          <div className={cx("buttonConatiner")}>
            <Button text="" type={BUTTON_TYPES.OUTLINE} />
          </div>
        );
      default:
      case OPENING:
        return null;
    }
  };

  useEffect(() => {
    window.scrollTo({ top: 0 });
  }, [nowScene]);

  return (
    <div className={cx("sprintIntro")} onClick={handleChangeScene}>
      <div ref={upperRef} className={cx("upper")}>
        <div>
          <Dialog
            speaker={ROLE_NAMES.EE}
            children={PARAGRAPH[nowScene]}
            isShowTriangle
          />
        </div>
        <Role name={ROLE_NAMES.EE} />
      </div>
      <div className={cx("content")}>{renderContent()}</div>
    </div>
  );
};
export default SprintIntro;
